<template>
  <div id="app">
    <el-container>
      <el-row>
        <h1 style="position: absolute;">SDplayer.club</h1>
      </el-row>
      <el-header height="500">
        <searchBlock @filterSearchData="searchDataRes" @finding="finding"></searchBlock>
      </el-header>

      <el-main>
        <el-row type="flex" justify="center">
          <RobotList :GundataRes="GundataRes" :filterSearchStart="filterSearchStart"></RobotList>
        </el-row>
        <el-row type="flex" justify="right">
          <el-popover placement="right" title="哭穷" width="400" trigger="click">
            <el-card class="box-card">
              <donateBlock></donateBlock>
            </el-card>
            <a slot="reference">
              <img src="zfb.png">
              <el-button type="info">作者的话</el-button>
            </a>
          </el-popover>
        </el-row>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import RobotList from "../../components/RobotList";
import searchBlock from "../../components/searchBlock";
import donateBlock from "../../components/donateBlock";

export default {
  name: "app",
  components: {
    RobotList: RobotList,
    searchBlock: searchBlock,
    donateBlock: donateBlock
  },
  data() {
    return {
      GundataRes: {},
      filterSearchStart: false
    };
  },
  methods: {
    searchDataRes(res) {
      let _this = this;
      _this.GundataRes = res;
    },
    finding(start) {
      this.filterSearchStart = true;
    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.donate-block {
  position: relative;
  float: right;
  top: -150px;
}
</style>
