  computed: {
    RankChip: {
      get: function() {
        let rank = this.filterPost.Rank;
        if (rank.length) {
          return true;
        } else {
          return false;
        }
      },
      set: function() {
        this.filterPost.Rank = [];
      }
    },
    spChip: {
      get: function() {
        let SP = this.filterPost.special;
        if (SP.length) {
          return true;
        } else {
          return false;
        }
      },
      set: function() {
        this.filterPost.special = [];
      }
    }
  },

  methods: {
    filterClear() {
      this.filterPost = {
        Rank: [],
        special: [],
        fightType: "",
        landType: "",
        Machine: "",
        nickname: "",
        page: 1,
        pagesize: 10
      };
    },
    onfilterSearch() {
      let _this = this;
      this.loading = true;
      _this.$emit("finding", true);
      axios
        .post(`/getGundam/FilterSearch`, this.filterPost)
        .then(res => {
          // console.log(res);
          _this.$emit("filterSearchData", res);
          _this.$emit("finding", false);
          _this.loading = false;
          // _this.gundams = _this.gundams.concat(res.data);
        })
        .catch(function(error) {
          _this.loading = false;
          // console.log(error);
        });
    }
  }
};
