<template>
  <el-container>
    <el-header type="flex" justify="center">
      <el-col :lg="24" :xs="0">
        <el-pagination
          :background="true"
          layout="total ,prev, pager, next, jumper"
          :total="postData.searchNum"
          :current-page="postData.page"
          @current-change="onPageChange"
          :small="xsPgenav"
        ></el-pagination>
      </el-col>
      <!-- 小导航 -->
      <el-col :lg="0" :xs="24">
        <el-pagination
          :background="true"
          layout="prev , pager,next"
          :total="postData.searchNum"
          :current-page="postData.page"
          @current-change="onPageChange"
          small
        ></el-pagination>
      </el-col>
    </el-header>
    <el-main>
      <el-row type="flex" justify="center">
        <el-col :lg="12">
          <el-table
            :data="gundams"
            style="width: 100%"
            :show-header="false"
            :row-style="{height:40}"
            v-loading="listLoading"
          >
            <el-table-column prop="avatarURL" label="avatarURL" :width="xsPgenav?100:180" size="small">
              <template slot-scope="scope">
                <a @click="openDialog(scope.row)" href="#">
                  <div class="img-responsive">
                    <img :src="scope.row.avatarURL">
                  </div>
                </a>
              </template>
            </el-table-column>
            <el-table-column prop="model" label="model" :width="xsPgenav?120:180"></el-table-column>
            <el-table-column prop="rank" label="rank" :width="xsPgenav?20:100"></el-table-column>
            <el-table-column prop="rating" label="rating" width="100" v-if="!xsPgenav"></el-table-column>
            <el-table-column label="能力值" v-if="!xsPgenav" :width="xsPgenav?20:180">
              <template slot-scope="scope">
                <table>
                  <tr>
                    <td>攻击</td>
                    <td>防御</td>
                    <td>机动</td>
                    <td>操控</td>
                    <td>4D综合</td>
                    <td>3D综合</td>
                  </tr>
                  <tr>
                    <td>{{scope.row.att}}</td>
                    <td>{{scope.row.def}}</td>
                    <td>{{scope.row.fly}}</td>
                    <td>{{scope.row.ctl}}</td>
                    <td>{{scope.row.D4sum}}</td>
                    <td>{{scope.row.D3sum}}</td>
                  </tr>
                </table>
              </template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>
    </el-main>
    <el-footer type="flex" justify="center">
      <el-col :lg="24" :xs="0">
        <el-pagination
          :background="true"
          layout="total ,prev, pager, next, jumper"
          :total="postData.searchNum"
          :current-page="postData.page"
          @current-change="onPageChange"
        ></el-pagination>
      </el-col>
      <!-- 小导航 -->
      <el-col :lg="0" :xs="24">
        <el-pagination
          :background="true"
          layout="prev , pager,next"
          :total="postData.searchNum"
          :current-page="postData.page"
          @current-change="onPageChange"
          small
        ></el-pagination>
      </el-col>
    </el-footer>
    <ddialog :show="showDialog" @dialogData="closeDialog" :gundam="detailRobot"></ddialog>
  </el-container>
</template>

<script>
import ddialog from "./dialog";
import axios from "axios";


export default {
  components: {
    ddialog: ddialog
  },
  data: () => ({
    showDialog: false,
    loading: false,
    detailRobot: {},
    gundams: [],
    pageInfo: {
      page: 1,
      pagesize: 10
    },
    postData: {
      page: 1,
      pagesize: 10,
      searchNum: 767
    }
  }),
  props: {
    GundataRes: Object,
    filterSearchStart: Boolean
  },
  watch: {
    GundataRes: function(newVal, oldVal) {
      // console.log(newVal);
      this.gundams = newVal.data.res;
      (this.pageInfo = {
        page: newVal.data.postData.page,
        pagesize: newVal.data.postData.page
      }),
        (this.postData = newVal.data.postData);
    }
  },
  computed: {
    listLoading: {
      get: function() {
        return this.loading && !this.filterSearchStart;
      },
      set: function(val) {
        this.loading = this.filterSearchStart = val;
      }
    },
    xsPgenav: {
      get: function() {
        let ScreenWidth = document.body.clientWidth;
        return ScreenWidth < 768 ? true : false;
      },
      set: function(val) {
        return val;
      }
    }
  },
  methods: {
    closeDialog(data) {
      this.showDialog = data; //子组件触发父组件事件，进行数据变更，同步result数据
      
    },
    openDialog(gundam) {
      this.showDialog = true;
      this.detailRobot = gundam;
    },
    loadMore() {
      let _this = this;
      this.busy = true;
      //把busy置位true，这次请求结束前不再执行
      setTimeout(() => {
        _this.pageInfo.page++;
        _this.loadPage();
        //调用获取数据接口，并且传入一个true，让axios方法指导是否需要拼接数组。
      }, 500);
    },
    loadPage() {
      let _this = this;
      let page = _this.pageInfo.page,
        pagesize = _this.pageInfo.pagesize;
      this.axios
        .get(`/getGundam?pagesize=${pagesize}&page=${page}`)
        .then(res => {
          // console.log(res);
          _this.gundams = _this.gundams.concat(res.data);
        });
    },
    onPageChange(val) {
      let _this = this;
      _this.loading = true;
      _this.postData.page = val;
      let pagesize = _this.postData.pagesize;
      this.axios
        // .get(`/getGundam?pagesize=${pagesize}&page=${val}`)
        .post(`/getGundam/FilterSearch`, _this.postData)
        .then(res => {
          // console.log(res);
          _this.gundams = res.data.res;
          _this.loading = false;
        });
    }
  },
  mounted() {
    this.loadPage();
  }
};
</script>

<style>
.img-responsive {
  max-width: 100%;
  height: 110px;
  padding-bottom: 10%;
  overflow: hidden;
}
.img-responsive img {
  width: 70%;
}
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}
</style>
