<template>
  <div
    v-loading="loading"
    element-loading-text="拼命加载中"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <el-dialog :visible.sync="getshow" width="60%" center :fullscreen="showFullDlg">
      <el-row>
        <el-col :lg="8" :xs="8">
          <img :src="gundam.AvatarUrl_TW">
        </el-col>
        <el-col :lg="6" :xs="14">
          <h2 class="h2">{{gundam.model}}</h2>
          <div>
            Rank:
            <strong class="h3">{{gundam.rank}}</strong>
          </div>
          <div>{{gundam.landType}} {{gundam.fightType}}</div>
        </el-col>
        <el-col :lg="10"></el-col>
      </el-row>
      <br>
      <el-row>
        <el-col :lg="12" :xs="24">
          <table width="90%">
            <tbody>
              <tr>
                <td colspan="6" align="center" bgcolor="#FFFFFF">机体数值</td>
              </tr>
              <tr>
                <td bgcolor="#666699">
                  <font color="#FFFFFF">攻击</font>
                </td>
                <td bgcolor="#666699">
                  <font color="#FFFFFF">防御</font>
                </td>
                <td bgcolor="#666699">
                  <font color="#FFFFFF">机动</font>
                </td>
                <td bgcolor="#666699">
                  <font color="#FFFFFF">操控</font>
                </td>
                <td bgcolor="#666699">
                  <font color="#FFFFFF">4D综合</font>
                </td>
                <td bgcolor="#666699">
                  <font color="#FFFFFF">3D综合</font>
                </td>
              </tr>
              <tr>
                <td bgcolor="#FFFFFF">{{gundam.att}}</td>
                <td bgcolor="#FFFFFF">{{gundam.def}}</td>
                <td bgcolor="#FFFFFF">{{gundam.fly}}</td>
                <td bgcolor="#FFFFFF">{{gundam.ctl}}</td>
                <td bgcolor="#FFFFFF">{{gundam.D4sum}}</td>
                <td bgcolor="#FFFFFF">{{gundam.D3sum}}</td>
              </tr>
              <tr>
                <td bgcolor="#666699">
                  <font color="#FFFFFF">繁体名称</font>
                </td>
                <td bgcolor="#666699">
                  <font color="#FFFFFF">冲刺时间</font>
                </td>
                <td bgcolor="#666699">
                  <font color="#FFFFFF">跳跃时间</font>
                </td>
                <td bgcolor="#666699">
                  <font color="#FFFFFF">索敌距离</font>
                </td>
                <!-- <td bgcolor="#666699">
                  <font color="#FFFFFF"> </font>
                </td>
                <td bgcolor="#666699">
                  <font color="#FFFFFF"> </font>
                </td>-->
              </tr>
              <tr>
                <td bgcolor="#FFFFFF">{{gundam.nameCN_G}}</td>
                <td bgcolor="#FFFFFF">{{gundam.rushTime}}</td>
                <td bgcolor="#FFFFFF">{{gundam.jumpTime}}</td>
                <td bgcolor="#FFFFFF">{{gundam.Radar_distance}}</td>
                <td bgcolor="#FFFFFF"></td>
                <td bgcolor="#FFFFFF"></td>
              </tr>
            </tbody>
          </table>
        </el-col>
        <el-col :lg="12" :xs="24">
          <table v-html="gundam.expTable" width="90%"></table>
        </el-col>
      </el-row>

      <el-row>
        <!-- weapon -->
        <el-col :lg="12" :xs="24">
          <table v-html="gundam.weaponTable"></table>
        </el-col>
        <!-- skill -->
        <el-col :lg="12" :xs="24">
          <el-table :data="gundam.skill" :show-header="false">
            <el-table-column prop="skill_name" label="skill_name">
              <template slot-scope="scope">
                <img :src="scope.row.skill_img" width="30">
                <strong>{{scope.row.skill_name}}</strong>
                <br>
                <span>{{scope.row.skill_detail}}</span>
              </template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>

      <!-- 合成需要 -->
      <p v-if="gundam.makeNeed&&gundam.makeNeed.length">合成需要：</p>
      <el-row type="flex" justify="space-around">
        <div class="img-responsive-sub" v-for="(robots) in gundam.makeNeed" :key="robots.ID">
          <a :title="robots.name" href="#" @click="updateGundam(robots)">
            <img :src="robots.img" :class="robots.role&&robots.role == 'key'? 'as-key':''">
            <p>{{robots.Level}}</p>
          </a>
        </div>
      </el-row>
      <!-- 国服合成需要 -->
      <p v-if="gundam.makeNeed_CN&&gundam.makeNeed_CN.length">国服合成需要：</p>
      <el-row type="flex" justify="space-around">
        <div class="img-responsive-sub" v-for="(robots) in gundam.makeNeed_CN" :key="robots.ID">
          <a :title="robots.name" href="#" @click="updateGundam(robots)">
            <img :src="robots.img" :class="robots.role&&robots.role == 'ke'? 'as-key':''">
            <p>{{robots.Level}}</p>
          </a>
        </div>
      </el-row>
      <!-- 用于合成 -->
      <p v-if="gundam.toMake&&gundam.toMake.length">用于合成：</p>
      <el-row v-if="gundam.toMake&&gundam.toMake.length" type="flex" justify="space-around">
        <div class="img-responsive-sub" v-for="(robots) in gundam.toMake" :key="robots.ID">
          <a :title="robots.name" href="#" @click="updateGundam(robots)">
            <img :src="robots.img" :class="robots.role&&robots.role == 'as-ke'? 'as-key':''">
            <p>{{robots.Level}}</p>
          </a>
        </div>
      </el-row>

      <!-- 扭蛋 -->
      <el-row>
        <span v-if="gundam.Machine&&gundam.Machine.length">扭蛋机：</span>
        <el-tag v-for="(Num, index) in gundam.Machine" :key="index">{{Num}}</el-tag>
      </el-row>
      <!-- 更多信息 -->
      <el-row>
        <p>其他信息：</p>
        <p>-势力：{{gundam.force}}</p>
        <p>-{{gundam.feature}}</p>
        <p>-出自作品：{{gundam.from}}</p>
        <p>-驾驶员：{{gundam.pilot}}</p>
        <p>{{gundam.story}}</p>
      </el-row>
      <el-button type="primary" @click="getshow = false">关闭详情</el-button>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      loading: false
    };
  },
  computed: {
    getshow: {
      get: function() {
        return this.show || false;
      },
      set: function() {
        this.$emit("dialogData", false);
      }
    },
    showFullDlg: {
      get: function() {
        let ScreenWidth = document.body.clientWidth;
        return ScreenWidth < 768 ? true : false;
      },
      set: function(val) {
        return val;
      }
    }
  },
  props: {
    show: Boolean,
    gundam: Object
  },
  updated: function() {
    var div = document.querySelector(".el-dialog__wrapper");
    // div.scrollTop = div.scrollHeight;
    div.scrollTop = 0;
  },
  methods: {
    updateGundam: function(robot) {
      this.loading = true;
      axios.post("/getGundam/findOne", robot).then(res => {
        this.gundam = res.data;
        this.loading = false;
        var div = document.querySelector(".el-dialog__wrapper");
        // div.scrollTop = div.scrollHeight;
        div.scrollTop = 0;
      });
    }
  }
};
</script>

<style>
.img-responsive {
  max-width: 100%;
  height: 110px;
  padding-bottom: 20%;
  overflow: hidden;
}
.img-responsive img {
  width: 42%;
}
.img-responsive-sub {
  max-width: 100%;
  height: 50px;
  padding-bottom: 10%;
  overflow: hidden;
}
.img-responsive-sub img {
  width: 80%;
}
.as-key {
  background: palevioletred;
}
</style>
